header {
  width: 100%;
  padding: 1rem;
  color: white;
  background-color: royalblue;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

section {
  padding: 1rem 2rem;
}

hr {
  border-color: lightgrey;
  border-style: dashed;
  margin: 0rem;
  padding: 0rem;
}

section h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

section h3 select {
  width: 6rem;
}

ul {
  list-style: none;
  padding: 0rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

ul > * {
  width: 23%;
  flex: 0 0 auto;
  max-width: 100%;
}

li {
  padding: 0.75rem 1rem;
  cursor: pointer;
  margin: 0.81rem;
  border-radius: 0.5rem;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

div.todo-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

